import React, { Component } from 'react';
import './css/aboutme.css';
import hero3 from './imges/clip-programming.png';
import img5 from './imges/html5.png';
import img6 from './imges/css3.png';
import img7 from './imges/JS1.svg';
import img8 from './imges/react.png';

export default class Aboutme extends Component {
	render() {
		return (
			<div>
				<div className="container3">
					<h1>Something about ME</h1>
					<hr className="hr3" />
					<hr className="hr4" />
					<div className="grid">
						<div className="image">
							<img src={hero3} />
						</div>
						<div className="aboutmetext">
							<ul>
								<li>
									<h3>
										<span>Translate</span> designs into web
									</h3>
									<p>With much practice and much work, now I can make any design alive. </p>
								</li>
								<li>
									<h3>
										<span> Fast</span> but professional
									</h3>
									<p>I can finsh my tasks in short time but with high quality.</p>
								</li>
								<li>
									<h3>
										<span>Working</span> with groups
									</h3>
									<p>I would Love to work with other people so I can pass and receive experiences.</p>
								</li>
							</ul>
							<div className="aboutmeimages">
								<ul>
									<li>
										<img src={img5} />
										<br />
										<p>HTML5</p>
									</li>
									<li>
										<img src={img6} />
										<br />
										<p>CSS3</p>
									</li>
									<li>
										<img src={img7} />
										<br />
										<p>JavaScript</p>
									</li>
									<li className="react">
										<img src={img8} />
										<br />
										<p>React.js</p>
									</li>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}
