import React, { Component } from 'react';
import Hero from './imges/banner-image.png';
import bg from './imges/backgroundimg.png';
import './css/home.css';

export default class Home extends Component {
	render() {
		return (
			<div>
				<body id="body">
					<section className="s1">
						<div className="container1">
							<img src={bg} className="bg1" />
							<div className="text">
								<h1>
									<span>Moh</span>ammed AbuSharekh
								</h1>
								<p>A Frontend Web Devloper, I Make the web more Beautiful</p>
							</div>
							<div className="image">
								<img src={Hero} className="me" />
							</div>
						</div>
					</section>
				</body>
			</div>
		);
	}
}
